:root {
  --primary-color: #e02b20;
  --primary-text-color: #454545;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  text-decoration: none;
  font-style: normal;
  color: var(--primary-text-color);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapper {
  width: 100%;
  max-width: 1408px;
  margin: 0 auto;
  padding: 0 16px;
}

.header {
  height: 115px;
}

.header__wrapper {
  border-bottom: 1px solid #cccccc;
  height: 100%;
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: auto;
}

.header__phone,
.header__address {
  display: flex;
  align-items: center;
}

.header__phone img,
.header__address img {
  display: block;
  height: 32px;
  flex: 0 0 32px;
  margin-right: 16px;
}

.header__phone a,
.header__address a {
  font-weight: bold;
  color: var(--primary-text-color);
  position: relative;
  top: 0;
  transition: color .4s ease;
}

.header__phone a:hover,
.header__address a:hover {
  color: var(--primary-color);
}

.header__phone a:after,
.header__address a:after {
  content: "";
  position: absolute;
  bottom: -2px;
  height: 1px;
  width: 0;
  left: 0;
  background: transparent;
  transition: width .4s ease;
}

.header__phone a:hover:after,
.header__address a:hover:after {
  width: 100%;
  background-color: var(--primary-color);
}

.header__address {
  margin-right: 48px;
}

.nav-bar {
  display: flex;
  height: 60px;
  align-items: center;
  justify-content: space-between;
}

.nav-bar ul {
  display: flex;
  position: relative;
  right: -1px;
}

.nav-bar li {
  display: block;
  height: 32px;
  width: 32px;
  margin-left: 12px;
  cursor: pointer;
  opacity: .5;
  transition: opacity .4s ease;
}

.nav-bar li:hover {
  opacity: 1;
}

.nav-bar nav span {
  display: inline-block;
  margin-right: 12px;
  cursor: pointer;
}

.promo {
  height: 500px;
  width: 100%;
  background-position: center -250px;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-color: #000000;
}

.promo .wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.promo__content {
  width: 40%;
  padding-top: 80px;
}

.promo h1,
.promo h2 {
  color: #ffffff;
}

.promo h1 {
  font-size: 48px;
  font-weight: 100;
}

.promo__titles {
  position: relative;
  padding: 0 0 16px 28px;
}

.promo__titles:before,
.promo li:before {
  content: "";
  background: var(--primary-color);
  height: 100%;
  width: 8px;
  position: absolute;
  left: 0;
  top: 0;
}

.promo ol {
  margin-top: 40px;
}

.promo li {
  padding-left: 24px;
  position: relative;
  color: #ababab;
  line-height: 28px;
}

.promo li:before {
  height: 1px;
  top: 50%;
}
